import { Dispatch, SetStateAction } from "react"
import { useComponentWidth } from "@rp/react/web/common/hooks"

const MINIMAL_LIST_BREAK_WIDTH = 550

type UseShouldUseMinimalistListState = {
  setDivRef: Dispatch<SetStateAction<HTMLDivElement | null>>
  shouldUseMinimalList: boolean
}

function useShouldUseMinimalList(): UseShouldUseMinimalistListState {
  const { width, setComponentRef } = useComponentWidth()

  return {
    setDivRef: setComponentRef,
    shouldUseMinimalList: width < MINIMAL_LIST_BREAK_WIDTH,
  }
}

export { useShouldUseMinimalList, MINIMAL_LIST_BREAK_WIDTH }
export type { UseShouldUseMinimalistListState }
